<template>
  <div key="profile" class="cards" style="height: 105%; padding-left:30px; margin-top:20px;">
    <div class="guide-container">
      <div class="fw-bold" style="font-size: 24px; font-family: 'KB_C2', sans-serif; margin-right: 10px; text-align: left;">M-Tip Í∞ÄÏù¥Îìú</div> 
      <div v-if="notices.length === 0" style="font-family: 'KB_C2', sans-serif; color: #4b4a4a; margin-top: 20px;">
        Îì±Ïû¨Îêú Í≤åÏãúÍ∏ÄÏù¥ ÏóÜÏäµÎãàÎã§.
      </div>
      <div v-else class="notice-item" v-for="notice in notices" :key="notice?.mtip_guide_no">
        <div class="notice-details">
          <div class="notice-content-wrapper">
            <div class="notice-content">{{ notice?.mtip_guide_content }}</div>
            <div v-if="notice.mtip_guide_file" class="file-download-wrapper">
              <a @click.prevent="downloadFile(notice)"
                 href="#"
                 class="file-download-link" style="color: #ffca05; margin-right: 2px;font-family: 'KB_C2', sans-serif; font-size: 17px;"
              >
                ÏÉÅÏÑ∏Î≥¥Í∏∞
              </a>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
  
  <script>
  import { mapGetters } from "vuex";
  import api from '@/api/axios';

  export default {
    name: "GuideCom",
    data() {
      return{
        notices: [] // Í≤åÏãúÍ∏Ä Î™©Î°ùÏùÑ Ï†ÄÏû•Ìï† Î∞∞Ïó¥
      }
    },
    computed: {
      ...mapGetters("login", ["getLoginInfo"]),
      loginInfo() {
        const info = this.getLoginInfo;
        return info
          ? {
              ...info,
              position_no: info.position_no || "",
              level_no: info.level_no || "",
              job_no: info.job_no || "",
              dp_no: info.dp_no || "",
            }
          : null;
      },
    },
    methods: {

      handleMouseDown(event) {
      event.target.style.color = '#f4ada4';
    },
    handleMouseUp(event) {
      event.target.style.color = '#f4ada4';
    },

    async fetchNotices() {
      try {
        console.log('m-tip Í≤åÏãúÍ∏Ä db Ï†ëÍ∑º');
        const response = await api.get('/mtip/list');
        console.log('m-tip Í≤åÏãúÍ∏Ä dbÏóêÏÑú Í∞ÄÏßÄÍ≥† Ïò® Îç∞Ïù¥ÌÑ∞:', response.data); // Í∞ÄÏ†∏Ïò® Îç∞Ïù¥ÌÑ∞ Ï∂úÎ†•
        this.notices = response.data.filter(notice => notice && notice.mtip_guide_no);
        console.log('Filtered notices:', this.notices); // ÌïÑÌÑ∞ÎßÅÎêú Îç∞Ïù¥ÌÑ∞ Ï∂úÎ†•
      } catch (error) {
        console.error('Error fetching notices:', error);
      }
    },

    async downloadFile(notice) {
  console.log('downloadFile Ìï®ÏàòÍ∞Ä Ìò∏Ï∂úÎêòÏóàÏäµÎãàÎã§.');
  try {
    console.log('Îã§Ïö¥Î°úÎìú ÏöîÏ≤≠ URL:', `/notice/downloadGuide/${notice.mtip_guide_file}`);
    const response = await api.get(`/notice/downloadGuide/${notice.mtip_guide_file}`, {
      responseType: 'blob',
    });
    console.log('ÏÑúÎ≤Ñ ÏùëÎãµ:', response);  // ÏÑúÎ≤Ñ ÏùëÎãµ Ï†ÑÏ≤¥Î•º Ï∂úÎ†•Ìï©ÎãàÎã§.
    const blob = new Blob([response.data], {
      type: response.headers['content-type'],
    });
    const url = URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.href = url;
    link.download = this.getDisplayFileName(notice.mtip_guide_file);
    link.click();
    URL.revokeObjectURL(url);
  } catch (error) {
    console.error('ÌååÏùº Îã§Ïö¥Î°úÎìú Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.', error.response ? error.response.data : error.message);
    if (error.response) {
      console.error('ÏÑúÎ≤Ñ ÏùëÎãµ ÏÉÅÌÉú ÏΩîÎìú:', error.response.status);
      console.error('ÏÑúÎ≤Ñ ÏùëÎãµ Îç∞Ïù¥ÌÑ∞:', error.response.data);
    }
  }
},




    getDisplayFileName(fileName) {
      return fileName.split('/').pop();
    },
  },
  created() {
    this.fetchNotices();
  }
  };
  </script>
  
  <style scoped>

  .guide-container {
  text-align: left;
}
.notice-item {
