<template>
  <div class="cards" style="margin-top:20px; padding: 20px;">
    <div class="m-tip-header">
      <h2 class="m-tip-title fw-bold">M-Tip</h2>
      <a href="/M_TipListView" class="view-all">Ï†ÑÏ≤¥Î≥¥Í∏∞</a>
    </div>
    <div class="notice-list">
      <div v-for="category in mileageCategories" :key="category" class="notice-item">
        <span class="notice-mile">{{ category }}</span>
        <template v-if="latestNotices[category]">
          <span class="notice-title">
            {{ truncateTitle(latestNotices[category].mtip_board_title) }}
            <span class="title-icon">new</span>
          </span>
          <span class="notice-date" style="margin-left: 50px; font-size: 14px;font-family: 'KB_C2', sans-serif; margin-left: auto; /* Ïò§Î•∏Ï™ΩÏúºÎ°ú Î∞ÄÏñ¥ÎÉÑ */
  margin-right: 0; /* Ïò§Î•∏Ï™Ω Ïó¨Î∞± Ï†úÍ±∞ */">{{ formatDate(latestNotices[category].mtip_board_date) }}</span>
        </template>
        <span v-else class="no-notice">Í≤åÏãúÍ∏ÄÏù¥ ÏóÜÏäµÎãàÎã§.</span>
      </div>
    </div>
  </div>
</template>

<script>
import api from '@/api/axios';

export default {
  name: 'M-TipCom',
  data() {
    return {
      notices: [],
      paginatedNotices: [],
      mileageCategories: ['HRD', 'Monthly Best', 'Monthly Base', 'HotTip','BEST PG', 'BEST Branch', 'ÏÜåÎπÑÏûê ÏßÄÏõê', 'Î¶¨Í∑∏ ÌÖåÏù¥Î∏î', 'Í∏∞ÌÉÄ']
    };
  },
  computed: {
    latestNotices() {
    const latest = {};
    this.mileageCategories.forEach(category => {
      const notice = this.paginatedNotices.find(n => (n.mile_name === category) || (category === 'Í∏∞ÌÉÄ' && (n.mile_name === null || n.mile_name === 'Í∏∞ÌÉÄ')));
      if (notice) {
        latest[category] = notice;
      }
    });
    return latest;
  },
  },
  methods: {
    truncateTitle(title) {
      if (title.length > 25) {
        return title.slice(0, 25) + '....';
      }
      return title;
    },
    formatDate(dateString) {
      const date = new Date(dateString);
      return date.toLocaleDateString();
    },
    async fetchNotices() {
      console.log('Mtiplist DB Î©îÏÜåÎìúÎ°ú Ïù¥Îèô ~ ');
      try {
        const response = await api.get('/mtip/Mtiplist');
        this.notices = response.data;
        this.paginatedNotices = this.notices; // ÌéòÏù¥ÏßÄÎÑ§Ïù¥ÏÖò Î°úÏßÅÏù¥ ÏóÜÏúºÎØÄÎ°ú Î™®Îì† Í≥µÏßÄÏÇ¨Ìï≠ÏùÑ ÌëúÏãú
        console.log('Mtiplist ÏÑúÎ≤ÑÏóêÏÑú Í∞ÄÏßÄÍ≥† Ïò® Í∞í :', this.notices);
      } catch (error) {
        console.error('Error fetching notices:', error.response ? error.response.data : error.message);
      }
    },
    async handleNoticeClick(notice) {
  console.log("notice:", notice);
  if (this.isProcessing) return;
  this.isProcessing = true;
  try {
    console.log("Í≤åÏãúÍ∏Ä ÏÉÅÏÑ∏Î≥¥Í∏∞+Ï°∞ÌöåÏàò Î©îÏÜåÎìú ÎèÑÎã¨", notice);
    // Ï°∞ÌöåÏàò Ï¶ùÍ∞Ä ÏöîÏ≤≠
    await api.post(`/notice/increment-views/${notice.notice_board_no}`);
    
    // Í≤åÏãúÍ∏Ä ÏÉÅÏÑ∏ Ï†ïÎ≥¥ ÏöîÏ≤≠
    const response = await api.get(`/notice/details/${notice.notice_board_no}`);
    console.log('Í≤åÏãúÍ∏Ä ÏÉÅÏÑ∏Î≥¥Í∏∞ ÏÑúÎ≤ÑÏóêÏÑú Í∞ÄÏßÄÍ≥† Ïò® Îç∞Ïù¥ÌÑ∞:', response); // ÏùëÎãµÏù¥ Ï†ïÏÉÅÏ†ÅÏúºÎ°ú Ïò§ÎäîÏßÄ ÌôïÏù∏
    const noticeDetails = response.data;
    console.log('Fetched notice details:', noticeDetails);

    // Ï°∞ÌöåÏàò ÏóÖÎç∞Ïù¥Ìä∏
    notice.notice_board_hit += 1;

    const noticeToPass = {
      ...noticeDetails,
      mile_no: noticeDetails.mile_no,
      mile_name: noticeDetails.mile_name,
      file: noticeDetails.file || null,
      notice_board_hit: notice.notice_board_hit // ÏóÖÎç∞Ïù¥Ìä∏Îêú Ï°∞ÌöåÏàò ÏÇ¨Ïö©
    };

    console.log('Navigating to noticeDetailView with notice:', {
      id: notice.notice_board_no,
      notice: noticeToPass
    });

    this.$router.push({
      name: 'noticeDetailView',
      params: { 
        id: notice.notice_board_no, 
        notice: noticeToPass
      }
    });
  } catch (error) {
    console.error('Error fetching notice details:', error.response ? error.response.data : error.message);
  } finally {
    this.isProcessing = false;
  }
},
  },
  mounted() {
    this.fetchNotices();
  }
};
</script>

