      <a href="/M_TipBestView" class="view-all">Ï†ÑÏ≤¥Î≥¥Í∏∞</a>
    </div>
    <div class="notice-list">
      <div v-for="notice in bestNotices" :key="notice.mtip_board_no" class="notice-item">
        <span class="notice-mile">{{ notice.mile_name || 'Í∏∞ÌÉÄ' }}</span>
