<script>
import api from '@/api/axios';

export default {
  name: 'M-TipCom',
  data() {
    return {
      bestNotices: [], // Ï¢ãÏïÑÏöîÍ∞Ä Í∞ÄÏû• ÎßéÏùÄ Í≤åÏãúÍ∏Ä Î™©Î°ù
    };
  },
  methods: {
    truncateTitle(title) {
      if (title.length > 25) {
        return title.slice(0, 25) + '....';
      }
      return title;
    },
    formatDate(dateString) {
      const date = new Date(dateString);
      return date.toLocaleDateString();
    },
    async fetchBestNotices() {
      console.log('Mtiplist DB Î©îÏÜåÎìúÎ°ú Ïù¥Îèô ~ ');
      try {
        const response = await api.get('/mtip/bestMtiplist');
        this.bestNotices = response.data.slice(0, 9); // Ï¢ãÏïÑÏöîÍ∞Ä ÎßéÏùÄ ÏÉÅÏúÑ 9Í∞úÏùò Í≤åÏãúÍ∏Ä
        console.log('Best Mtiplist ÏÑúÎ≤ÑÏóêÏÑú Í∞ÄÏßÄÍ≥† Ïò® Í∞í :', this.bestNotices);
      } catch (error) {
        console.error('Error fetching best notices:', error.response ? error.response.data : error.message);
      }
    },
  },
  mounted() {
    this.fetchBestNotices();
  }
};
</script>

